<template>
  <v-app>
    <mb-right-sidebar></mb-right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right dark>
          <v-breadcrumbs :items="dirs" class="breadcrumbs">
            <template v-slot:item="{ item }">
              <v-breadcrumbs-item class="breadcrumbs-item-text" :to="item.link"> {{ item.text }} </v-breadcrumbs-item>
            </template>
          </v-breadcrumbs>
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
          <v-tab :key="2"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="advancedView ? advancedHeaders : pointHeaders"
              :items="device.points"
              class="elevation-1 text-start"
              item-key="uuid"
              show-select
              :item-class="backgroundColor"
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
              :show-expand="advancedView"
              single-expand
              @item-expanded="expandItem"
              :expanded="expanded"
              :loading="loadingData"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>POINTS</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <!-- Refresh btn-->
                    <v-btn @click="onClickRefresh" x-small color="primary" class="white--text"> Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn >&nbsp;
                    <!-- Create btn-->
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="success" class="white--text" dark v-bind="attrs" v-on="on" >Create<v-icon x-small right dark> mdi-plus </v-icon></v-btn >
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="createEditItem">Modbus Point</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="dialogPreMade = true; selectedPoints = []; searchPrePoints = ''">Pre Made Point</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu >&nbsp;
                    <!-- Delete btn -->
                    <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text" > Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn>&nbsp;
                    <!-- Download btn -->
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="#009688" class="white--text" dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn >
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points, 'json')">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points, 'excel')">Excel</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(device.points, 'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu >&nbsp;
                    <!-- Import btn -->
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on" >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn >
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click=" dialogImportExcel = true;" >Excel</v-list-item-title >
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <!-- Export btn -->
                    <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon> </v-btn>&nbsp;
                    <!-- advanced view -->
                    <v-btn @click="advancedView = !advancedView; expanded = []" x-small color="purple" class="white--text" > Advanced View <v-icon x-small right dark> {{advancedView ? 'mdi-playlist-minus': 'mdi-playlist-plus'}} </v-icon> </v-btn>&nbsp;
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                </v-toolbar>
              </template>

              <template v-slot:item.enable="{ item }">
                <v-checkbox v-model="item.enable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:item.writable="{ item }">
                <v-checkbox v-model="item.writable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:item.write_value_once="{ item }">
                <v-checkbox v-model="item.write_value_once" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:item.priority_array_write="{ item }" >
                <div class="overflow-td">
                  <span v-for="(key, index) of item.priority_array_write ? Object.keys(item.priority_array_write): []" :key="index" >
                    {{key}}:&nbsp;<span class="value">{{ item.priority_array_write[key] || item.priority_array_write[key] == 0 ? item.priority_array_write[key] : 'null' }}</span>,&nbsp;
                  </span>
                </div>
              </template>
              <template v-slot:item.fault_message="{ item }" >
                <div class="overflow-td">
                  {{item.fault_message}}
                </div>
              </template>
              <template v-slot:item.value_raw="{ item }" >
                <div class="overflow-td">
                  {{item.value_raw}}
                </div>
              </template>
              <template v-slot:item.created_on="{ item }" >
                {{item.created_on | timezone}}
              </template>
              <template v-slot:item.updated_on="{ item }" >
                {{item.updated_on | timezone}}
              </template>
              <template v-slot:item.ts_value="{ item }" >
                {{item.ts_value | timezone}}
              </template>
              <template v-slot:item.ts_fault="{ item }" >
                {{item.ts_fault | timezone}}
              </template>
              <template v-slot:item.register="{ item }">
                <v-edit-dialog :return-value.sync="item.register" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
                  <b>{{ item.register }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.register" single-line type='number' min="0" @input="numberChange()" ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.register_length="{ item }">
                <v-edit-dialog :return-value.sync="item.register_length" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.register_length }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.register_length" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.function_code="{ item }">
                <v-edit-dialog :return-value.sync="item.function_code" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.function_code }}</b>
                  <template v-slot:input>
                    <v-select v-model="item.function_code" :items="point_types" ></v-select>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.data_type="{ item }">
                <v-edit-dialog :return-value.sync="item.data_type" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.data_type }}</b>
                  <template v-slot:input>
                    <v-select v-model="item.data_type" :items="point_data_types"></v-select>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.data_endian="{ item }">
                <v-edit-dialog :return-value.sync="item.data_endian" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.data_endian }}</b>
                  <template v-slot:input>
                    <v-select v-model="item.data_endian" :items="point_data_endians"></v-select>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.value_round="{ item }">
                <v-edit-dialog :return-value.sync="item.value_round" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.value_round }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.value_round" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>

            <template v-slot:item.value_operation="{ item }">
              <v-edit-dialog :return-value.sync="item.value_operation" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.value_operation }}</b>
                <template v-slot:input>
                  <v-text-field v-model="item.value_operation" single-line type='string'></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

              <template v-slot:item.cov_threshold="{ item }">
                <v-edit-dialog :return-value.sync="item.cov_threshold" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.cov_threshold }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.cov_threshold" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.input_min="{ item }">
                <v-edit-dialog :return-value.sync="item.input_min" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.input_min }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.input_min" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.input_max="{ item }">
                <v-edit-dialog :return-value.sync="item.input_max" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.input_max }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.input_max" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.scale_min="{ item }">
                <v-edit-dialog :return-value.sync="item.scale_min" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.scale_min }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.scale_min" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.scale_max="{ item }">
                <v-edit-dialog :return-value.sync="item.scale_max" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.scale_max }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.scale_max" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.value="{ item }">
                <v-edit-dialog :return-value.sync="item.value" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.value }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.value" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>

              <template v-slot:item.actions="{ item }">
                <v-icon small @click="createEditItem(item)" color="primary"> mdi-pencil </v-icon>
                <v-icon small @click="clickDelete(item)" color="error"> mdi-delete </v-icon>
                <v-icon small @click="openPointWriteDialog(item)" color="orange">mdi-yelp</v-icon>
              </template>

              <!-- header -->
              <template v-slot:header.enable="{ item }">
                {{ toUpperTextKey('enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('enable'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.writable="{ item }">
                {{ toUpperTextKey('writable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('writable')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.write_value_once="{ item }">
                {{ toUpperTextKey('write_value_once') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('write_value_once')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.register_length="{ item }">
                {{ toUpperTextKey('register_length') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('register_length')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.data_type="{ item }">
                {{ toUpperTextKey('data_type') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('data_type')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.data_endian="{ item }">
                {{ toUpperTextKey('data_endian') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('data_endian')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.value_round="{ item }">
                {{ toUpperTextKey('value_round') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('value_round')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.value_operation="{ item }" >
                {{ toUpperTextKey('value_operation') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('value_operation')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.cov_threshold="{ item }">
                {{ toUpperTextKey('cov_threshold') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('cov_threshold'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.input_min="{ item }">
                {{ toUpperTextKey('input_min') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('input_min')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.input_max="{ item }">
                {{ toUpperTextKey('input_max') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('input_max')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.scale_min="{ item }">
                {{ toUpperTextKey('scale_min') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('scale_min')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.scale_max="{ item }">
                {{ toUpperTextKey('scale_max') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('scale_max')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.value="{ item }">
                {{ toUpperTextKey('value') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('value'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>

              <!-- expand-edit-row -->
              <template v-slot:expanded-item="{ headers, item }">
                <td colspan="2"></td>
                <td class="actions">
                  <v-icon small @click="updatePoint(editedItem)" color="primary"> mdi-content-save </v-icon>
                </td>
                <td class="name">
                  <v-text-field v-model="editedItem.name" required :rules="[() => !isExistingvalue(editedItem, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </td>
                <td class="driver"></td>
                <td class="enable">
                  <v-checkbox v-model="editedItem.enable" ></v-checkbox>
                </td>
                <td class="Repeat Point Write">
                  <v-checkbox v-model="editedItem.write_value_once" ></v-checkbox>
                </td>
                <td class="function_code">
                  <v-select v-model="editedItem.function_code" :items="point_types" required :rules="[rules.required]"></v-select>
                </td>
                <td class="register">
                  <v-text-field v-model="editedItem.register" :rules="[() => !isExistingvalue(editedItem, 'register') || 'Register Existed']" type="number" ></v-text-field>
                </td>
                <td class="register_length">
                  <v-text-field v-model="editedItem.register_length" type="number" ></v-text-field>
                </td>
                <td class="cov_threshold">
                  <v-text-field v-model="editedItem.cov_threshold" type="number"></v-text-field>
                </td>
                <td class="data_type">
                  <v-select v-model="editedItem.data_type" :items="point_data_types" required :rules="[rules.required]" ></v-select>
                </td>
                <td class="data_endian">
                  <v-select v-model="editedItem.data_endian" :items="point_data_endians" required :rules="[rules.required]" ></v-select>
                </td>
                <td class="value">
                  <v-text-field v-model="editedItem.value" type="number"></v-text-field>
                </td>
                <td class="value_original"></td>
                <td class="value_round">
                  <v-text-field v-model="editedItem.value_round" type="number"></v-text-field>
                </td>
                <td class="value_operation">
                <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                </td>
                <td class="value_raw"></td>
                <td class="input_max">
                  <v-text-field v-model="editedItem.input_max" type="number"></v-text-field>
                </td>
                <td class="input_min">
                  <v-text-field v-model="editedItem.input_min" type="number"></v-text-field>
                </td>
                <td class="scale_max">
                  <v-text-field v-model="editedItem.scale_max" type="number"></v-text-field>
                </td>
                <td class="scale_min">
                  <v-text-field v-model="editedItem.scale_min" type="number"></v-text-field>
                </td>
                <td class="writable">
                  <v-checkbox v-model="editedItem.writable"></v-checkbox>
                </td>
                <td colspan="9"></td>
              </template>

              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="1">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
          <v-tab-item :key="2">
            <system-status-table server='modbus'></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="device.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field @input="changeName" v-model="editedItem.name" label="Point name" required :rules="[() => !isExistingvalue(editedItem, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.writable" label="writable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.write_value_once" label="Repeat Point Write"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.cov_threshold" label="cov threshold" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select v-model="editedItem.function_code" label="function_code" :items="point_types" required :rules="[rules.required]" v-on:change="$refs.form.resetValidation()" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select v-model="editedItem.data_type" label="data type" :items="point_data_types" required :rules="[rules.required]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select v-model="editedItem.data_endian" label="data endian" :items="point_data_endians" required :rules="[rules.required]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.register" label="register" :rules="[() => !isExistingvalue(editedItem, 'register') || 'Register Existed']" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.register_length" label="register length" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point round/math" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value" label="value" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_round" label="value round" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="validWizard" ref="form_wizard" lazy-validation>
              <v-data-table
                :headers="advancedHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
                :loading="savingImport"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item, 'uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createPoint(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item, 'uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.name="{ item, index }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item, 'uuid')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else @input="(val)=>changeName(val, index)" v-model="item.name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.register="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item, 'uuid', item)" v-model="item.register" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.register" type="number" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item, 'register', item) || 'Register Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.enable="{ item }">
                  <v-checkbox v-model="item.enable"></v-checkbox>
                </template>
                <template v-slot:item.function_code="{ item }">
                  <v-select v-model="item.function_code" :items="point_types" required :rules="[rules.required]" v-on:change="$refs.form.resetValidation()" ></v-select>
                </template>
                <template v-slot:item.value="{ item }">
                  <v-text-field v-model="item.value" type="number" single-line style="width: 100px" ></v-text-field>
                </template>
                <template v-slot:item.priority_array_write="{ item }">
                  {{item.priority_array_write && typeof item.priority_array_write == 'object' ? JSON.stringify(item.priority_array_write) : item.priority_array_write}}
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!validWizard || savingImport">Save</v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-select v-if="editField === 'data_type'" v-model="editAll[editField]" :items="point_data_types" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'data_endian'" v-model="editAll[editField]" :items="point_data_endians" :label="toUpperTextKey(editField)" ></v-select>
          <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
          <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
          <v-text-field v-if="typeof editAll[editField] === 'string' && editField !== 'function_code' && editField !== 'data_type' && editField !== 'data_endian'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Pre Made Point Dialog -->
    <v-dialog v-model="dialogPreMade" max-width="1000px">
      <v-card>
        <v-card-title>
          <div class="headline">Create Points</div>
        </v-card-title>
        <v-card-text style="max-height:40vh; overflow: auto;">
          <v-form v-model="valid" ref="form" lazy-validation >
            <v-row>
              <v-col >
                <template v-if="!modbusPoints.length">
                  No pre made points.
                </template>
                <template v-else>
                  <v-text-field v-model="searchPrePoints" append-icon="mdi-magnify" label="Search" single-line hide-details class="mb-1"></v-text-field>
                  <v-treeview v-model="selectedPoints" :items="modbusPoints" selectable return-object open-all class="text-left" @input="updatedSelected" activatable ref="tree" :search="searchPrePoints"></v-treeview>
                </template>
              </v-col>
              <v-divider vertical></v-divider>
              <v-col cols="8" >
                <template v-if="!selectedPoints.length">
                  No points selected.
                </template>
                <template v-else>
                  <div v-for="(key, index) of Object.keys(selectedPointsShow)" :key="index" class="mb-4 pa-2">
                    <h3 class="mb-2" style="opacity: 0.6;">{{getDeviceNameByDeviceId(key)}}</h3>
                    <v-row >
                      <v-col cols="12" sm="6" md="6" v-for="(point, idx) of selectedPointsShow[key]" :key="idx">
                        <v-text-field v-model="point.value.name" :label="`pre point: ${point.name}`" required :rules="[() => !isExistingvalue(point.value, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                      </v-col>
                    </v-row><br/>
                    <v-divider></v-divider>
                  </div>
                </template>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogPreMade = false"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="savePoints" :disabled="!valid || selectedPoints.length == 0"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Point Write Dialog -->
    <point-write-dialog 
      :is-open="dialogPointWrite" 
      :on-close="()=>dialogPointWrite = false" 
      :selected-point="editedItem"
      :api="'modbus'"
      :response-from-dialog="(response)=>responseFromDialog(response)"
    ></point-write-dialog>

  </v-app>
</template>

<script>
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import MbRightSidebar from './MbRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import PointWriteDialog from '../Common/PointWriteDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, TOPIC, POINT_DEFAULT, POINT_HEADERS, POINT_ADVANCED_HEADERS, POINT_TYPES, POINT_DATA_TYPES, POINT_DATA_ENDIANS, HISTORY_DATA_TYPES, POINT_EDIT_ALL_DEFAULT, } from './modbusConstant';
import { isJsonString, copyToClipboard, exportJSON, exportCSV, exportExcel, toUpperTextKey, convertPointsValueToString, conventionNaming } from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    MbRightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog,
    PointWriteDialog,
    SystemStatusTable,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: TOPIC,
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    point_types: [...POINT_TYPES],
    point_data_types: [...POINT_DATA_TYPES],
    point_data_endians: [...POINT_DATA_ENDIANS],
    history_data_types: [...HISTORY_DATA_TYPES],
    editAll: {...POINT_EDIT_ALL_DEFAULT},
    editedItem: {...POINT_DEFAULT},
    rules: {...RULES},
    tableProps: {...DATA_TABLE_PROPS},
    dialog: false,
    dialogDelete: false,
    dialogPointWrite: false,
    dialogImport: false,
    dialogImportExcel: false,
    dialogMassEdit: false,
    dialogPreMade: false,
    tab: 0,
    editedIndex: -1,
    action: '',
    device: {},
    valid: false,
    selected: [],
    dataImport: '',
    importArray: [],
    e1: 1,
    search: '',
    searchWizard: '',
    editField: "",
    intervalId: null,
    expanded: [],
    isClickSaveImport: false,
    dirs: [],
    advancedView: false,
    modbusPoints: [],
    selectedPoints:[],
    selectedPointsShow:[],
    searchPrePoints: '',
    validWizard: false,
    savingImport: false,
    loadingData: false,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Point' : 'Edit Point';
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getData();
      }
    },
    selectedPoints(val,oldVal) {
      val.forEach(point=>{
        point.value.name = point.name;
      });
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
      next();
    });
  },
  mounted() {
    this.getModbusPoints();
    this.getData();
    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getData();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getData() {
      this.loadingData = true;
      this.device = await axios.get(`point-server/device/${this.$route.params.id}?with_children=true`);
      this.getDirs();
      this.device.points = this.device.points.map(item => {
        const keys = Object.keys(item.point_store);
        if (keys && keys.length > 0) {
          keys.forEach(key => {
            item = { ...item, [key]: item.point_store[key] };
          });
        }
        delete item.point_store;
        delete item.point_uuid;
        return item;
      });
      this.loadingData = false;
    },
    getDirs(){
      axios.get(`point-server/network/${this.device.network_uuid}`).then((data)  => {
        this.dirs= [{text: 'NETWORKS', link: '/modbus/networks'}];
        this.dirs.push({text: `NET: ${data.name}`, link: `/modbus/networks/${data.uuid}/devices`});
        this.dirs.push({text: `DEV: ${this.device.name}`});
      });
    },
    createEditItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({},this.editedIndex == -1 ? {...POINT_DEFAULT, device_uuid: this.device.uuid} : item);
      this.expanded= []
      this.dialog = true;
      if(this.$refs.form ){
        this.$refs.form.resetValidation();
        this.valid = false;
      }
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updatePoint(this.editedItem);
        } else {
          this.createPoint(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.device.points.indexOf(this.editedItem);
      this.device.points.splice(this.editedIndex, 1);
      await axios.delete(`point-server/point/${this.editedItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, {...POINT_DEFAULT, device_uuid: this.device.uuid});
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, {...POINT_DEFAULT, device_uuid: this.device.uuid});
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport= false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form_wizard.validate()) {
          this.savingImport = true;
          this.isClickSaveImport = true;
          try{
            await this.createMutiPoints(this.importArray);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){
          }finally{
            this.savingImport = false;
          }
        }
      }
    },
    clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.device_uuid = this.device.uuid;
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point=>{
        delete point.uuid;
        delete point.value_raw;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(item, fieldName) {
      let value = item[fieldName];
      let points = _cloneDeep(this.device.points);
      if (this.editedIndex !== -1) {
        points.splice(this.editedIndex, 1); //when edit
      }
      if (fieldName === 'register') {
        value = Number(value);
        points = [...points].filter(p => p.function_code === item.function_code);
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      // const data = JSON.parse(JSON.stringify(array)); //clone deep
      // data.forEach(point=>{
      //   if(point.priority_array_write){
      //     point.priority_array_write = `"${JSON.stringify(point.priority_array_write).replace("\"", "\'")}"`;
      //   }
      // });
      const data = convertPointsValueToString(JSON.parse(JSON.stringify(array)));
      if (type === 'csv') {
        return exportCSV('points', data);
      }
      if (type === 'excel') {
        return exportExcel('points', data);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(data));
      }
    },
    updatePoint(data) {
      axios.put(`point-server/point/${data.uuid}`, data)
        .then(response => {
          const index = this.device.points.findIndex(n => n.uuid == response.uuid);
          Object.assign(this.device.points[index], response);
          this.$toasted.show('update success');
          this.close();
          this.expanded = [];
        });
    },
    async createPoint(data) {
      await axios.post(`point-server/point`, data)
        .then(response => {
          this.device.points.push(response);
          if (this.importArray && this.importArray.length > 0) {
            const index = this.importArray.findIndex(n => n.name == response.name);
            if(index > -1){
              Object.assign(this.importArray[index], response);
            }
          }
          this.$toasted.show('create success');
          this.close();
        });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor + ' set-row-height';
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = {...POINT_EDIT_ALL_DEFAULT};
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({item}) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh(){
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    getModbusPoints() {
      axios.get('modbus').then(r => {
        if(!r || !r.length) return;
        const devices = r;
        this.modbusPoints = [{id: 1, name:"Select All", children: []}];
        devices.forEach(device=>{
          if(device.points && device.points.length > 0){
            const deviceChild = [];
            device.points.forEach(point=>{
              deviceChild.push({id: point._id, name: point.name, value: point} );
            });
            this.modbusPoints[0].children.push({id: device._id, name: `Device: ${device.name}`, children: deviceChild});
          }
        });
      })
    },
    async savePoints(){
      if (this.$refs.form.validate()) {
        const points = this.selectedPoints.map(p=>p.value);
        points.forEach(point=>{
          if(point.uuid) delete point.uuid;
        });
        this.dialogPreMade = false;
        await this.createMutiPoints(points);
      }
    },
    async createMutiPoints(points) {
      const promises=[];
      for(const item of points) {
        item.device_uuid = this.device.uuid; //update to the current device_uuid
        item.register_length=item.register_length? item.register_length:0;
        if(item.priority_array_write && typeof item.priority_array_write == 'string'){
          item.priority_array_write = JSON.parse(item.priority_array_write);
        }
        if(!item.uuid) {
          promises.push(this.createPoint(item));
        }
      }
      await Promise.all(promises);
    },
    updatedSelected(){
      this.selectedPointsShow = this.selectedPoints.reduce((grs, currPoint) => {
        grs[currPoint.value.device_id] ? grs[currPoint.value.device_id].push(currPoint) : grs[currPoint.value.device_id] = [currPoint];
        return grs;
      }, {});
    },
    getDeviceNameByDeviceId(device_id){
      const devices = this.modbusPoints[0].children;
      return (devices.find(d=>d.id == device_id)).name;
    },
    changeName(val, index){
      if(index > -1){
        this.importArray[index].name = conventionNaming(val, this.setting); //for wizzard dialog only
      }else{
        this.editedItem.name = conventionNaming(val, this.setting); //for create-edit dialog only
      }
    },
    openPointWriteDialog(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogPointWrite = true;
    },
    responseFromDialog(point_updated){
      const index = this.device.points.findIndex(p => p.uuid == point_updated.uuid);
      Object.assign(this.device.points[index], point_updated);
    }
  },
};
</script>
<style scoped>
.centered-input >>> input {
  text-align: center;
}
.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}
.background-fault-true {
  background-color: #fbb93e;
}
.background-enable-false {
  background-color: #666666;
}
